import Detail from '../public/detail'
import None from '../public/noNumber'
import Loading from '../public/Loading'
import Head from '../public/head'
import LoadingImg from '../public/loadingImg'
  export default {
      name: "experienceCentre",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          imgUrl:'../../static/img/static/images/allptp.png',
          localList:[],
          left:0,
          isPrev:false,
          isNext:true,
          sliderIndex:0,
          hightList:[],
          experList:[],
          pickerOptions2: {
            disabledDate:(time)=> {
              return time.getTime() < Date.now() - 8.64e7;
            }
          },
          date: '',
          person:'',
          addClick:false,
          otherList:[
            {person:'成人+',adult:0},
          ],
          tiyan:'',
          closeIndex:'',
          price:[0,0],
          total:0,
          HighLoading:false,
          isLoading:false,
          searchList:'',
          editSearch:'搜索',
          content:'',
          allprice:''
        }
    },
    components:{
        Head,
      Loading,
      LoadingImg,
      None,
      Detail
    },
    watch:{
      content:function(){
        if(!this.content){
          this.searchAll()
        }
      },
      person:function(){
        if(this.person < 17){
          this.searchAll()
        }else{
          this.addClick = true
        }
      },
      date:function(){
        this.searchAll()
      },
      allprice:function(){
        this.searchAll()
      },
      tiyan:function(){
        this.searchAll()
      },
      closeIndex:function(){
        this.searchAll()
      },
      sliderIndex:function(){
        if(this.sliderIndex > 0 && this.sliderIndex < this.localList.length - 4){
          this.isPrev = true
          this.isNext = true
        }else if(this.sliderIndex == this.localList.length - 4){
          this.isPrev = true
          this.isNext = false
        }else{
          this.isPrev = false
          this.isNext = true
        }
      }
    },
    methods:{
      moreActive(item,index){
        this.$router.push({
          path: 'moreActive',
          query:{
            key: item.city,
            information: JSON.stringify(item)
          }
        })
      },
      searchHome(){
        this.searchAll()
      },
      getVal(msg){
        this.content = msg
      },
      toptop(item){
        this.$router.push({
          path: '/classification',
          query:{
            information: item.value,
            name: item.title
          }
        })
      },
      getKind(){
        this.$http.post(this.api + '/home/Kind/kindlist',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              let a = []
              let b = []
              for(let j in data){
                a.push(data[j])
              }
              for(let i =0;i<a.length;i++){
                b.push({img:a[i].image_id?a[i].domain+a[i].image_url:'',title:a[i].kind_name,value:a[i].kind_id})
              }
              this.searchList = b
            }else if(res.data.code == 3){
              this.$http.post(this.api+ '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getKind()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
        //查看详情
      toPublish(item,index){
        this.$router.push({
          path: '/publishPage',
          query: {
            information: item.activity_id
          }
        })
      },
      searchAll(){
          this.getActive(this.pageIndex,this.price[0],this.price[1],this.date?this.date[0]/1000:0,this.date?this.date[1]/1000:0,this.tiyan,this.person,this.closeIndex)
          this.getAllHigh(this.pageIndex,this.price[0],this.price[1],this.date?this.date[0]/1000:0,this.date?this.date[1]/1000:0,this.tiyan,this.person,this.closeIndex)
      },
      getActive(val,price_low,price_high,begin_time,end_time,kind_id,person,volunteen){
        this.isLoading = true
        this.$http.post(this.api + '/home/Activity/activ_list',{
          token: localStorage.getItem('token'),
          keywords: this.content,
          page:val,
          price_low:price_low,
          price_high:price_high,
          activ_begin_time:	begin_time,
          activ_end_time:end_time,
          kind_id:kind_id,
          max_person_num:person,
          is_volunteen:volunteen
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.experList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getActive(val,price_low,price_high,begin_time,end_time,kind_id)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
          .catch(error=>{
            this.isLoading = false
          })
      },
      toActivestep(){
        this.$router.push('/activeStep')
      },
      prevIndex(){
        this.sliderIndex -= 1
        this.left += 240
      },
      nextIndex(){
        this.sliderIndex += 1
        this.left -= 240
      },
      rejest(){
        this.price = [0,0]
      },
      show(){
        this.$nextTick(() => {
          this.$refs.saveDateInput.focus()
        })
      },
      reduce(item,index){
        if(index == 0){
          if(item.adult > 0){
            this.addClick = false
            item.adult = item.adult -1
            let b =0
            for(let i = 0;i<this.otherList.length;i++){
              b = b + this.otherList[i].adult
            }
            this.person = b
          }
        }else{
          if(item.adult !=0){
            this.addClick = false
            item.adult=item.adult -1
            let b =0
            for(let i = 0;i<this.otherList.length;i++){
              b = b + this.otherList[i].adult
            }
            this.person = b
          }else{
            item.adult = 0
          }
        }
      },
      add(item,index){
        if(this.person < 16){
          this.addClick = false
          item.adult = item.adult + 1
          let b =0
          for(let i = 0;i<this.otherList.length;i++){
            b = b + this.otherList[i].adult
          }
          this.person = b
        }else{
          this.addClick = true
        }

      },
      change(){
        if(this.closeIndex){
          this.closeIndex = ''
        }else{
          this.closeIndex = 1
        }
      },
      getHotPlace(){
        this.$http.post(this.api + '/PopularCity',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              this.localList = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getHotPlace()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      getAllHigh(val,price_low,price_high,begin_time,end_time,kind_id,person,volunteen){
        this.HighLoading = true
        this.$http.post(this.api + '/home/Activity/activ_list',{
          token: localStorage.getItem('token'),
          page: val,
          sort: 1,
          price_low:price_low,
          price_high:price_high,
          keywords: this.content,
          activ_begin_time:	begin_time,
          activ_end_time:end_time,
          kind_id:kind_id,
          max_person_num:person,
          is_volunteen:volunteen
        })
          .then(res=>{
            if(res.data.code == 1){
              this.hightList = res.data.data.data
              this.total = res.data.data.total
              this.HighLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getAllHigh(val,price_low,price_high,begin_time,end_time,kind_id,person,volunteen)
                })
            }else if(res.data.code == 0){
              alert(res.data.data)
            }
          })
      },
      getAllSJ(val){
        this.isLoading = true
        this.$http.post(this.api + '/home/Activity/activ_list',{
          token: localStorage.getItem('token'),
          page: val,
        })
          .then(res=>{
            if(res.data.code == 1){
              this.experList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getAllSJ(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.data)
            }
          })
      },
      handleCurrentChange(val){
        this.getAllHigh(val)
        this.getAllSJ(val)
        this.getHotPlace()
        window.scroll(0,0)
      },
      chooseMoney(){
        this.allprice = this.price
      }
    },
    mounted() {
        window.scroll(0,0)
      this.getHotPlace()
      this.getAllSJ(1)
      this.getAllHigh(1)
      this.getKind()
    },
    created() {
        this.content = this.$route.query.information
      if(this.$route.query.information){
        this.editSearch = this.$route.query.information
      }
    }
  }



